* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  min-width: 320px;
  margin-bottom: 16px;
}

ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: 500;
  font-size: 20px;
}

li {
  display: flex;
  align-items: center;
  gap: 10px;
}

.country-card {
  display: flex;
  gap: 10px;
  align-items: center;
  font-weight: 500;
  font-size: 30px;
}

.country-name {
  margin: 0;
}